.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.featured-item {
  flex: 1;
  margin: 0 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 15px -10px #000000;
  box-shadow: 0px 0px 15px -10px #000000;
}
.featured-title {
  font-size: 20px;
}
.featured-money-container {
  margin: 10px 0;
  display: flex;
  align-items: center;
}
.featured-money {
  font-size: 30px;
  font-weight: bold;
}
.featured-money-rate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}
.featured-icon {
  font-size: 14px;
  margin-left: 5px;
  color: green;
}
.featured-icon.negatif {
  color: red;
}
.featured-sub {
  font-size: 15px;
  color: gray;
}
