.product-list {
  flex: 4;
}
.product-list-item {
  display: flex;
  align-items: center;
}
.product-list-img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.product-list-edit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: #3bb077;
  color: white;
  cursor: pointer;
  margin-right: 15px;
}
.product-list-delete {
  color: red;
  cursor: pointer;
}
